body {
	margin: 0;
	color: $font-color;
	font-family: $font-style;
	font-weight: 300;
	font-size: 1.1em;
	line-height: 1.6;
	-webkit-font-smoothing: auto;
	background-color: #FFFFFF
}
h1,
h2,
h3 {
	font-weight: 600;
	color: $heading-font-color;
	font-family: $heading-font;
	line-height: 2;
}
h1 {
	color: $main;
	font-size: 2.5em;
	a,
	a:visited {
		text-decoration: none;
		color: $main;
	}
}
h2 {
	font-size: 2em;
	margin: 10;
}
h4 {
	text-decoration: none;
}
a,
a:visited {
	color: $link-color;
	text-decoration: underline;
	/*font-weight: bold;*/
}
a:hover {
	color: $link-hover-color;
	text-decoration: underline;
}
header {
	background: $header;
	padding: 15px 10px;
	text-align: center;
	margin: 0 0 0;
	height: 0vh;
	display: flex;
	justify-content: center;
	align-items: center;
}
main {
	padding: 0 15px;
	max-width: $content-width;
	margin: 0 auto;
}
time {
	color: #898989;
	margin: 0;
}
.container {
	max-width: $content-width;
	margin: 0 auto;
}

/* Jordan Mods */

blockquote {
  background: $blockquote-color;
  margin-left: 1.0em 10px;
  padding: 0.2em 10px;
}

.boxed {
  border: 2px solid #F0F0F0 ;
  padding: 5px 5px;
  background-color: #FFF;
}
.left {
 float: left;
 padding: 0 20px 20px 0;
}

/* research page collapsibles*/

.collapsible {
  display: block;

  input {
    position: absolute;
    left: -9999px;
    &:focus ~ .collapser{
      border-color: #F0F0F0;
    }
  }

  .collapser {
    cursor: pointer;
    border: 1px transparent dotted;
  }

  .arrow {
    float: right;
    display: inline-block;
    transform: rotate(90deg);
  }

  .collapsed {
    font-size: 0;
    margin: 0;
    opacity: 0;
    padding: 0;
  }
}
  input:checked ~ .arrow{
    transform: rotate(270deg);
  }

  input:checked ~ .collapsed {
    font-size: 16px;
    opacity: 1;
    height: auto;
    padding: 2px 0;
  }
  .arrow {
    transition: transform .25s ease-out;
  }

  .collapsed {
    /* fade out, then shrink */
    transition:   
      opacity .25s,
      margin .5s 0.25s,
      font-size .5s 0.25s,
      padding .5s .25s;
  }

  input:checked ~ .collapsed {    
    /* grow, then fade in */
    transition:   
      margin .25s,
      padding .25s,
      font-size .25s,
      opacity .5s .25s;
  }

/* Aside */
aside {
	position: fixed;
	top: 0;
	left: 0;
	background: #fff;
	width: 100%;
	z-index: 2;
	border-bottom: 1px solid lighten($light, 30%);
	box-shadow: 0px 1px 1px rgba(4, 25, 54, .1);
  
	nav {
	  width: 100%;
	  max-width: 800px;
	  margin: 0 auto;
  
	  ul {
		margin: 0;
		padding: 0;
		list-style: none;
		display: flex; /* Use Flexbox */
  
		li {
		  a {
			text-decoration: none;
			display: block;
			padding: 15px;
			font-family: 'Noto Sans', sans-serif;
			line-height: 20px;
			margin-bottom: -1px;
			box-shadow: 0;
  
			&:hover,
			&:active {
			  background: $header;
			  text-decoration: none;
			  color: $main;
			}
		  }
		}
  
		.float-left {
		  margin-right: auto; /* Pushes the first li to the left */
		}
  
	  }
  
	  &:after {
		content: '';
		display: table;
		clear: both;
	  }
	}
  }
  
  .container {
	max-width: $content-width;
	margin: 0 auto;
  }

footer {
	position: relative;
	bottom: 0;
	left: 0;
	text-align: center;
	padding: 200px;
}
